@import url(https://fonts.googleapis.com/css?family=Lato|Raleway&display=swap);
.Navbar{position:fixed;top:0;left:0;right:0;height:110px;background:#64bfc8;-webkit-transition:0.3s all ease-in;transition:0.3s all ease-in;font-family:"Lato", sans-serif}.Navbar.Navbar--collapsed{height:90px}.Navbar.Navbar--collapsed .Navbar__Container .Navbar__Logo img{-webkit-transform:scale(0.6);transform:scale(0.6)}.Navbar .Navbar__Container{position:relative;width:100%;height:100%;max-width:1280px;margin:0 auto;padding:0 30px;display:flex;justify-content:space-between;align-items:center;box-sizing:border-box}.Navbar .Navbar__Container .Navbar__SubContainer{display:flex;justify-content:space-around;width:calc(50% - 75px)}.Navbar .Navbar__Container .Navbar__SubContainer .Navbar__Link{color:#FCDA67;font-size:24px}.Navbar .Navbar__Container .Navbar__Logo{position:absolute;top:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.Navbar .Navbar__Container .Navbar__Logo img{width:150px;-webkit-transition:0.3s all ease-in;transition:0.3s all ease-in;-webkit-transform-origin:center top;transform-origin:center top}

a{text-decoration:none}

